(() => {
	// --- elements ---
	const mobileMenu = document.querySelector('.js-menu-container');
	const openBtn = document.querySelector('.js-open-menu');
	const smoothLinks = document.querySelectorAll('a[href^="#"]');
	const menuLinks = document.querySelectorAll('.menu-box-link');

	// ÑÐºÑ‰Ð¾ ÐºÐ»ÑŽÑ‡Ð¾Ð²Ð¸Ñ… ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ–Ð² Ð½ÐµÐ¼Ð°Ñ” â€” Ð·ÑƒÐ¿Ð¸Ð½ÑÑ”Ð¼Ð¾ÑÑŒ
	if (!mobileMenu || !openBtn) return;

	// --- scroll lock helpers ---
	// Ð³Ð»Ð¾Ð±Ð°Ð»ÑŒÐ½Ð¸Ð¹ lock ÑÑ‚Ð¾Ñ€Ñ–Ð½ÐºÐ¸ (Ð²Ð¸ÐºÐ¾Ñ€Ð¸ÑÑ‚Ð¾Ð²ÑƒÑ”Ñ‚ÑŒÑÑ Ñ– Ð² Ð¼Ð¾Ð´Ð°Ð»ÐºÐ°Ñ…)
	const lockBody = () => document.body.classList.add('body-lock');
	const unlockBody = () => document.body.classList.remove('body-lock');

	// --- menu toggle ---
	// Ð²Ñ–Ð´ÐºÑ€Ð¸Ñ‚Ñ‚Ñ / Ð·Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ Ð¼ÐµÐ½ÑŽ + Ð±Ð»Ð¾Ðº ÑÐºÑ€Ð¾Ð»Ñƒ
	const toggleMenu = () => {
		const isOpen = openBtn.getAttribute('aria-expanded') === 'true';

		openBtn.setAttribute('aria-expanded', !isOpen);
		openBtn.classList.toggle('burger-active');
		mobileMenu.classList.toggle('is-open');

		!isOpen ? lockBody() : unlockBody();
	};

	// --- open button ---
	openBtn.addEventListener('click', toggleMenu);

	// --- responsive reset ---
	// ÑÐºÑ‰Ð¾ ÐµÐºÑ€Ð°Ð½ ÑÑ‚Ð°Ð² desktop â€” Ð·Ð°ÐºÑ€Ð¸Ð²Ð°Ñ”Ð¼Ð¾ Ð¼ÐµÐ½ÑŽ
	window.matchMedia('(min-width: 768px)').addEventListener('change', e => {
		if (!e.matches) return;

		mobileMenu.classList.remove('is-open');
		openBtn.classList.remove('burger-active');
		openBtn.setAttribute('aria-expanded', false);
		unlockBody();
	});

	// --- smooth scroll for anchors ---
	smoothLinks.forEach(link => {
		link.addEventListener('click', e => {
			e.preventDefault();

			const target = document.querySelector(link.getAttribute('href'));
			target?.scrollIntoView({
				behavior: 'smooth',
				block: 'start',
			});
		});
	});

	// --- close menu when clicking navigation links ---
	menuLinks.forEach(link => link.addEventListener('click', toggleMenu));
})();